<template>
  <div style="height:100%;">
    <!-- <div ref="refmovingstyle" :style="onmovingstyle" class="movingstyle" style="background-image: url('/svgrepo-com/sring.svg')"></div> -->
    <div ref="onchart" style v-on:mousemove="showCoords($event)">
      <ve-line :data="chartData"></ve-line>
    </div>
    <div :style="styleUnderChart" style="position: relative;width:100%">
      <p>
        <span style="color: green">Random Line</span> - Add a new random number every half second
      </p>
      <p>
        <span style="color: blue">Mouse_Touch Line</span> - Set new value by moving the mouse or touching on the chart. The value will be updated every half second.
        will add
        <img
          class="namemenu"
          style="width:25px"
          src="/svgrepo-com/sring.svg"
        />
      </p>
      <flowinghh :config="flowingsetting" @updatetopleft="updatetopleft" />
    </div>
  </div>
</template>

<script>
import VeLine from "v-charts/lib/line.common";
import flowinghh from "../flowinghh";
export default {
  components: { VeLine, flowinghh },
  props: {
    appWidth: Number,
    appHeight: Number,
    appHeightView: Number
  },
  data() {
    return {
      styleUnderChart: {},
      counter: 1,
      interval: null,
      chartData: {
        columns: ["times", "Random", "Mouse_Touch"],
        rows: []
      },
      chartX: 0,
      chartY: 0,
      chartHeight: 0,
      isUser: false,
      makesure5: 14, //should have 5 in 30 times
      userNumber: 5,
      flowingbelowchartW: 0,
      flowingbelowchartH: 0,
      flowingNumber: 5,
      onmovingstyle: {
          width:"30px",
          height:"30px"},
      movingSteps: 0
    };
  },
  computed: {
    flowingsetting() {
      return {
        flowingW: this.flowingbelowchartW,
        flowingH: this.flowingbelowchartH,
        flowingN: this.flowingNumber
      };
    }
  },
  mounted() {
    // let belowchart = this.$refs.belowchart.getBoundingClientRect();
    // this.belowchartH = belowchart.height;
    // this.belowchartW = belowchart.width;

    // console.log("--belowchartH->",this.belowchartW,this.belowchartH);

    let chartrect = this.$refs.onchart.getBoundingClientRect();
    this.chartX = chartrect.x;
    this.chartY = chartrect.y;
    this.chartHeight = chartrect.height;

    //---Seting the height of  styleUnderChart
    // appWidth: Number,
    // appHeight: Number
    if (this.appHeight == 0) {
      this.belowchartH = this.appHeightView - this.chartHeight;
    } else {
      this.belowchartH = this.appHeight - this.chartHeight;
    }
    this.styleUnderChart = { height: this.belowchartH + "px" };
    this.belowchartW = this.appWidth;

    this.flowingbelowchartW = this.belowchartW;
    this.flowingbelowchartH = this.belowchartH;

    console.log("--VV->", this.flowingsetting);

    //---end
    // let dovalue=this.flowinghhSetting();
    // let tempvalue=dovalue;
    // dovalue=tempvalue;
    //     console.log("--tempvalue->",tempvalue);
    // console.log("--->",this.chartY,this.chartHeight);
    this.chartData.rows = [];
    this.counter = 1;
    for (; this.counter < 31; this.counter++) {
      var newNumber = (Math.random() * 5).toFixed(0);
      this.chartData.rows.push({ times: this.counter.toString(), Random: newNumber, Mouse_Touch: 5 });
      this.counter++;
    }

    this.interval = setInterval(() => {
      // if (this.counter > 30) {
      // clearInterval(this.interval);
      // } else {
      // console.log("--> before", this.chartData.rows);
      this.chartData.rows.shift();
      var newNumber = (Math.random() * 5).toFixed(0);

      if (this.isUser) {
        this.isUser = false;
      }

      this.makesure5++;
      if ((newNumber != 5) & (this.makesure5 > 14)) {
        this.makesure5 = 0;
        newNumber = 5;
      } else if (newNumber == 5) {
        this.makesure5 = 0;
      }
      // console.log("-->makesure5-->",this.makesure5);

      this.chartData.rows.push({ times: this.counter.toString(), Random: newNumber, Mouse_Touch: this.userNumber });
      //

      this.counter++;

      // }
    }, 500);
  },  
  methods: {
    updatetopleft: function(value) {
      // console.log("=======emit========>", value);
      this.flowinghhTopLeft=value;
    },
    showCoords(event) {
      //var x = event.clientX;
      var y = event.clientY;
      //var coords = "X coords: " + x + ", Y coords: " + y;
      // var mm= this.$refs.refflowinghh.getBoundingClientRect();

      this.isUser = true;
      y = y - this.chartY;
      if (y < this.chartHeight * 0.2375) {
        this.userNumber = 5;
      } else if (y < this.chartHeight * 0.3625) {
        this.userNumber = 4;
      } else if (y < this.chartHeight * 0.4875) {
        this.userNumber = 3;
      } else if (y < this.chartHeight * 0.6125) {
        this.userNumber = 2;
      } else if (y < this.chartHeight * 0.7375) {
        this.userNumber = 1;
      } else {
        this.userNumber = 0;
      }
this.flowingNumber = this.userNumber;
//       if ((this.flowingNumber != this.userNumber)) {
//         this.onmovingstyle = {
//           width:"30px",
//           height:"30px",
//           top: y - this.chartY + "px",
//           left: event.clientX - this.chartX + "px"
//         };
//         this.movingSteps = 0;

//         this.interval = setInterval(() => {
//           this.movingSteps++;
//           if (this.movingSteps > 5) {
//             this.flowingNumber = this.userNumber;
//             clearInterval(this.interval);
//           } else {
//             let movingRect = this.$refs.refmovingstyle.getBoundingClientRect();

// // this.flowinghhTopLeft
//             this.onmovingstyle = {
//           width:30/this.movingSteps+"px",
//           height:30/this.movingSteps+"px",
//               top: (this.flowinghhTopLeft.top +400+movingRect.y)/2 + "px",
//               left: (this.flowinghhTopLeft.left+movingRect.x )/2 +"px"
//             };
//             // console.log("==flow=====<", this.flowinghhTopLeft.left);
//             // console.log("==onmoving=====<", this.onmovingstyle);
//           }
//         }, 100);
      // }
    }
  }
};
</script>

